export default class Stars {
  constructor (ctx, width, height, amount) {
    this.ctx = ctx
    this.width = width
    this.height = height
    this.stars = this.getStars(amount)
  }

  // 
  getStars (amount) {
    let stars = []
    while (amount--) {
      // console.log(amount)
      // JSONstars
      stars.push({
        x: Math.random() * this.width,
        y: Math.random() * this.height,
        r: Math.random() + 0.2
      })
    }
    return stars
  }

  // 
  draw () {
    let ctx = this.ctx
    ctx.save()   // canvas
    ctx.fillStyle = 'white'
    // star starsstar function
    this.stars.forEach(star => {
      ctx.beginPath()  //
      ctx.arc(star.x, star.y, star.r, 0, 2 * Math.PI)  // 
      ctx.fill()  // 
    })
    ctx.restore() // 
  }

  // 10
  blink () {
    // map  
    this.stars = this.stars.map(star => {
      let sign = Math.random() > 0.5 ? 1 : -1
      star.r += sign * 0.2
      if (star.r < 0) {
        star.r = -star.r
      } else if (star.r > 1) {
        star.r -= 0.2
        // console.log(star.r)
      }
      return star
    })
  }
}



